* {
    font-family: montserrat;
}

body {
    background: #f1fbff;
}

/* ....................................carousel */
.section-padding {
    padding: 100px 0;
}

.carousel-item {
    height: 100vh;
    max-height: 500px;
}

.carousel-caption {
    bottom: 70px;
    z-index: 2;
}

.carousel-caption h5 {
    font-size: 45px;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-top: 25px;
}

.carousel-caption p {
    width: 60%;
    margin: auto;
    font-size: 18px;
    line-height: 1.9;
}

.carousel-inner::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    z-index: 1;
}

/* ...............navbar */

.navbar-nav a {
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 500;
}
.navbar-nav a:hover {
   color: rgb(238, 219, 12);
}

.navbar-light .navbar-brand {
    color: #000;
    font-size: 25px;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 2px;
}

.navbar-light .navbar-brand:focus,
.navbar-light .navbar-brand:hover {
    color: #000;
}

.navbar-light .navbar-nav .navbar-link {
    color: #000;
}

.w-100 {
    height: 100vh;
}

/* ..............service................... */

.section-padding {
    padding: 50px 0;
}

.services .card-body {
    font-size: 40px;
}




/* ............................... responsive media queries */

@media only screen and (max-width:768px) and (max-width:991px) {
    .carousel-caption{
        bottom:20px;
    }
    .carousel-caption p{
        width: 100%;
    }
    .card{
        margin-bottom: 30px;
    }
    
}

@media only screen and (max-width: 767px) {
    .navbar-nav {
        text-align: center;
    }

    .carousel-caption {
        bottom: 125px;
    }

    .carousel-caption h5 {
        font-size: 17px;
    }

    .carousel-caption a {
        padding: 10px 15px;
    }

    .carousel-caption p {
        bottom: 60px;
        line-height: 1.6;
        font-size: 12px;
        /* width: 100%; */
    }
    .card{
        margin-bottom: 30px;
    }
    .row{
        gap: 15px;
    }
    .about-text{
        padding-top: 30px;
    }
    .services{
        margin-top: -100px;
    }
}